/* AddProduct.css */
form {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background: #fff;
  font-family: 'Arial', sans-serif;
}

.background {
  background-color: #333;
  padding: 20px;
}

form div {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

input[type="text"],
input[type="number"],
input[type="file"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

input[type="file"] {
  padding: 5px;
}

input[type="text"]:focus,
input[type="number"]:focus,
input[type="file"]:focus {
  outline: none;
  border-color: #007bff;
}

.buttonCompras {
  width: 100%;
  padding: 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  margin: 5px;
  transition: transform 0.3s ease;
}

img:hover {
  transform: scale(1.1);
}

img[selected="true"] {
  border: 3px solid #007bff;
}

.suggested-images {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

input[type="file"]::file-selector-button {
  background: #007bff;
  border: none;
  padding: 10px 20px;
  margin-right: 10px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

input[type="file"]::file-selector-button:hover {
  background-color: #0056b3;
}

/* Media Queries */
@media (max-width: 768px) {
  form {
    padding: 15px;
  }

  .buttonCompras {
    padding: 10px;
    font-size: 14px;
  }

  input[type="text"],
  input[type="number"],
  input[type="file"] {
    font-size: 14px;
    padding: 8px;
  }

  img {
    width: 80px;
    height: 80px;
  }

  input[type="file"]::file-selector-button {
    padding: 8px 15px;
  }
}

@media (max-width: 480px) {
  form {
    padding: 10px;
  }

  .buttonCompras {
    padding: 8px;
    font-size: 12px;
  }

  input[type="text"],
  input[type="number"],
  input[type="file"] {
    font-size: 12px;
    padding: 6px;
  }

  img {
    width: 60px;
    height: 60px;
  }

  input[type="file"]::file-selector-button {
    padding: 6px 10px;
  }

  label {
    margin-bottom: 5px;
  }

  form div {
    margin-bottom: 15px;
  }
}
