.signin-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  min-height: 100vh;
  color: white;
}

.signin-background-image {
  width: 50%;
  height: 100vh;
  object-fit: cover;
}

.signin-form {
  width: 50%;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form-group {
  margin-bottom: 15px;
}

.form-input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: none;
  background: #333;
  color: white;
}

.signin-button {
  width: 100%;
  padding: 10px;
  background: #555;
  color: white;
  border: none;
  cursor: pointer;
}

.signin-button:hover {
  background: #777; 
}
